.mobile-menu {
  display: none;
  width: 32px;
  height: 37px;
  margin-top: 13.5px;
  cursor: pointer;
}
@media screen and (max-width: 992px) {
  .mobile-menu {
    display: block;
  }
}
.mobile-menu > .material-icons {
  font-size: 32px;
  height: 37px;
  line-height: 37px;
}
.mob-modal {
  position: fixed;
  top: 64px;
  right: -100%;
  height: 100%;
  width: 300px;
  background: rgba(0, 0, 0, .9);
  padding: 100px 20px 0 20px;
  transition: right .3s;
  z-index: 9;
}
.mob-modal-content {
  padding-left: 30px;
  position: relative;
}
.mob-modal-content > a {
  font-size: 18px;
  display: block;
  color: #ffab40;
  margin-bottom: 10px;
  padding: 5px 10px;
  transition: background .2s;
}
.mob-modal-content > a:hover {
  background: #262626;
  color: #AA9777;
}
/*============ Skill bar==============*/
.skillbar {
  position: relative;
  display: inline-block;
  width: 100%;
  background: #eee;
  height: 20px;
  border-radius: 3px;
  margin-bottom: 9px;
}

.skillbar-title {
  position: absolute;
  top: 0;
  left: 0;
  font-weight: bold;
  font-size: 11px;
  color: #ffffff;
  border-top-left-radius: 1px;
  border-bottom-left-radius: 1px;
  background: #262626;
  padding: 0 20px;
  height: 20px;
  line-height: 20px;
  letter-spacing: .7px;
}
.html {
  width: 80%;
}
.css {
  width: 85%;
}
.sass {
  width: 50%;
}
.btst {
  width: 60%;
}
.mtrl {
  width: 50%;
}
.js {
  width: 40%;
}
.jq {
  width: 40%;
}
.rj {
  width: 30%;
}
.ps {
  width: 40%;
}
.ai {
  width: 40%;
}
.wp {
  width: 50%;
}
.skillbar-bar {
  height: 20px;
  width: 0px;
  background: #6adcfa;
  border-radius: 3px;
  display: inline-block;
}

.skill-bar-percent {
  position: absolute;
  right: 10px;
  top: 0;
  font-size: 11px;
  height: 20px;
  line-height: 20px;
  color: #ffffff;
  color: rgba(0, 0, 0, 0.4);
}